import React, {useCallback, useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import {stateType} from "../../redux/store";
import {booksPageType} from "../../redux/searchReducer";
import {booksAPI} from "../../api/getBookApi";
import styles from "../../App.module.css";
import preloader from "../../common/preloader.gif";
import {Book} from "./Book";
import {addSearchResults, changeIsLoadingStatus, renewSearchResults, setErrorMessage} from "../../redux/actions";
import {booksType} from "../../redux/booksReducer";

export const Books = React.memo(() => {
    console.log('from Books')
    //data
    const booksState = useSelector<stateType, booksType>(state => state.books)
    const searchState = useSelector<stateType, booksPageType>(state => state.searchResults)
    const dispatch = useDispatch()
    const onClickHandler = useCallback(() => {
        dispatch(changeIsLoadingStatus(true))
        setTimeout(() => {
            booksAPI.getBooks(searchState.queryString,
                searchState.pageSize,
                booksState.books.length,
                searchState.sort,
                searchState.filter)
                .then(data => {
                    if (data.totalItems === -1) {
                        dispatch(setErrorMessage(data.kind))
                    } else {
                        dispatch(addSearchResults(data.items))
                    }
                })
        }, 2000)
    }, [dispatch, searchState, booksState.books.length])
    //useEffect
    useEffect(() => {
        if (searchState.queryString) {
            dispatch(changeIsLoadingStatus(true))
            setTimeout(() => {
                booksAPI.getBooks(searchState.queryString,
                    searchState.pageSize,
                    0,
                    searchState.sort,
                    searchState.filter)
                    .then(data => {
                        if (data.totalItems === -1) {
                            dispatch(setErrorMessage(data.kind))
                        } else {
                            dispatch(renewSearchResults(data.items, data.totalItems))
                        }
                    })
            }, 2000)
        }
    }, [searchState, dispatch])
    return (
        <div className={styles.booksWrapper}>
            {
                booksState.totalCount > 0 &&
                <React.Fragment>
                        <h2 className={styles.pagesCount}>Found {booksState.totalCount} results</h2>
                        <div className={styles.booksContainer}>
                            {
                                booksState.books.map((book, index) => {
                                    console.log(book.volumeInfo.categories)
                                    return (
                                        <Book key={index} {...book.volumeInfo}/>
                                    )
                                })
                            }
                        </div>
                </React.Fragment>
            }
            <LoadingOrLoadMore isLoading={booksState.isLoading}
                               totalCount={booksState.totalCount}
                               onClickHandler={onClickHandler}
                               />
            {
                booksState.error &&
                <ErrorMessage message={booksState.error}/>
            }
        </div>
    )
})

type ErrorMessagePropsType = {
    message: string,
}
export const ErrorMessage: React.FC<ErrorMessagePropsType> = React.memo((props) => {
    console.log('from error')
    return (
        <div className={styles.errorMessage}>
            <span className={styles.errorText}>
                {
                    props.message
                }. Try again.
            </span>
        </div>
    )
})

type LoadingOrLoadMorePropsType = {
    isLoading: boolean,
    totalCount: number,
    onClickHandler: () => void
}
export const LoadingOrLoadMore: React.FC<LoadingOrLoadMorePropsType> = React.memo((props) => {
    console.log('from LoadOrLoad')
    return (
        <React.Fragment>
            {
                props.isLoading ?
                    <div className={styles.preloader}>
                        <img src={preloader} alt={'preloader'}/>
                    </div> :
                    props.totalCount > 0 &&
                    <h3 className={styles.loadMore}
                        onClick={props.onClickHandler}>Load more</h3>
            }
        </React.Fragment>
    )
})